import React from 'react';
import { TabbarItem, Tabbar, Separator, Button, Header, Div, Card, CardGrid, Cell, platform, IOS, Panel, Group, PanelHeader, PanelHeaderButton } from '@vkontakte/vkui';

// иконки
import Icon20EducationOutline from '@vkontakte/icons/dist/20/education_outline';
//иконки
import Icon28AirplayVideoOutline from '@vkontakte/icons/dist/28/airplay_video_outline'; //обучение
import Icon28LocationOutline from '@vkontakte/icons/dist/28/location_outline'; //такси
import Icon28BugOutline from '@vkontakte/icons/dist/28/bug_outline'; //covid
import Icon28NewsfeedOutline from '@vkontakte/icons/dist/28/newsfeed_outline'; //новости
import Icon28HomeOutline from '@vkontakte/icons/dist/28/home_outline'; //организации
import Icon12Verified from '@vkontakte/icons/dist/12/verified'; //подтвержденные новости

//иконки назад
import Icon28ReplyOutline from '@vkontakte/icons/dist/28/reply_outline';
import Icon28LogoVkOutline from '@vkontakte/icons/dist/28/logo_vk_outline';
import Icon24LogoVk from '@vkontakte/icons/dist/24/logo_vk';

const osname = platform();


const Education = props => (
    <Panel id={props.id}>
        <PanelHeader
        left={<PanelHeaderButton>
        {osname === IOS ? <Icon28AirplayVideoOutline/> : <Icon28AirplayVideoOutline/>}
    </PanelHeaderButton>}
        >
         Обучение
        </PanelHeader>

        <Group>
        <CardGrid>
<Card size="l" mode="shadow">
        <Cell expandable before={<Icon20EducationOutline width={28} height={28} />}description="Подключено" onClick={props.go} data-to="example">Пример</Cell>
        <div style={{ height: 1 }} />
</Card>
</CardGrid>
<br/>
        <CardGrid>
<Card size="l" mode="shadow">
        <Cell expandable before={<Icon20EducationOutline width={28} height={28} />}description="Недоступно">Школа №2</Cell>
        <div style={{ height: 1 }} />
</Card>
</CardGrid>
<br/>
<CardGrid>
<Card size="l" mode="shadow">
        <Cell expandable before={<Icon20EducationOutline width={28} height={28} />}description="Недоступно">Школа №3</Cell>
        <div style={{ height: 1 }} />
</Card>
</CardGrid>
    </Group>
    <br/>
    <br/>
    <CardGrid>
<Card size="l" mode="outline">
    {<Header mode="secondary">Нет вашей школы?</Header>}
          <Div style={{ paddingTop: 1, paddingBottom: 12, color: 'black' }}>
          Почему вашей школы нет в списке или она не открывается?
          <br/>
          <br/>
          Какие классны можно подключить?
          <br/>
          Можно подключить 9-11 классы.
          <br/>
          <br/>
          Причина этой проблемы в том, что мы не получаем от вашей школы данных.
          В список данных входят контакты и предметы, которые были вам заданы.
          Если школа запрещает нам публиковать контактные данные, то мы публикуем только задания.
          Чтобы подключить школу к приложению, достаточно нажать на кнопку "Подключить"
          </Div>
          {<Header mode="secondary">В чем разница от сайта?</Header>}
          <Div style={{ paddingTop: 1, paddingBottom: 12, color: 'black' }}>
          Большая часть подростков, проводит свою жизнь в соц. сетях.
          <br/>
          <br/>
          Проще будет не покидая социальной сети, проверить домашнее задание, не переходя в браузер.

          <br/>
          <br/>
          <Button size="xl" mode="outline" onClick={props.go} href="https://vk.cc/atc27s">Подключить</Button>
          </Div>
          </Card>
</CardGrid>
<br/>
<br/>
<br/>
<br/>
<br/>
<br/>
       <Tabbar>
        <Separator wide />
        <TabbarItem text="Организ." onClick={props.go} data-to="organizations"><Icon28HomeOutline />
        </TabbarItem>
        <TabbarItem text="COVID" onClick={props.go} data-to="covid"><Icon28BugOutline />
        </TabbarItem>
        <TabbarItem text="Новости" onClick={props.go} data-to="home"><Icon28NewsfeedOutline />
        </TabbarItem>
        <TabbarItem text="Обучение" onClick={props.go} data-to="education"><Icon28AirplayVideoOutline />
        </TabbarItem>
        <TabbarItem text="Такси" onClick={props.go} data-to="taxi"><Icon28LocationOutline />
        </TabbarItem>
     </Tabbar>

    </Panel>
);

export default Education;
