import React from 'react';
import { View, ConfigProvider } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Home from './panels/Home'
import Organizations from './panels/Organizations'
import Covid from './panels/Covid'
import Weather from './panels/Weather'
import News from './panels/News'
import Education from './panels/Education'
import Taxi from './panels/Taxi'
import Example from './panels/Example'
import RoomOne from './panels/RoomOne'
import RusLang from './class/classnine/Rus'




import bridge from '@vkontakte/vk-bridge'

bridge.send('VKWebAppInit');

class App extends React.Component {
    constructor(props) {
        super(props)
        this.state={
            ActivePanel: "home"

        }
    }
    componentDidMount() {
      bridge.subscribe(({ detail: { type, data }}) => {
        if (type === 'VKWebAppUpdateConfig') { // Получаем тему клиента.
          this.setState({scheme: data.scheme})
        }
      })
    }
    UpdateTheme() {
       if(this.state.scheme === "bright_light" || this.state.scheme === "client_light" ) {
       // Если в скейте такие темы:
           this.setState({scheme: 'space_gray'});
           // Меняем тему на альтернативную
           bridge.send("VKWebAppSetViewSettings", {"status_bar_style": "light", "action_bar_color": "#000"});
           // Изменяем цвет статус-бара и экшен-бара
       } else if(this.state.scheme === "space_gray" || this.state.scheme === "client_dark"){
           this.setState({scheme: 'bright_light'});
           // Меняем тему на альтернативную.
           bridge.send("VKWebAppSetViewSettings", {"status_bar_style": "dark", "action_bar_color": "#fff"});
           // Изменяем цвет статус-бара и экшен-бара
         }
    }

    go = e => {
        this.setState({ActivePanel: e.currentTarget.dataset.to})
        this.state = {
        scheme: "bright_light" // Если нам не придет тема - ставим эту по умолчанию.
}
}
    render() {
        return (
<ConfigProvider isWebView={true} scheme={this.state.scheme}>
           <View activePanel={this.state.ActivePanel} >
           <Home id="home" go={this.go} />
           <Organizations id="organizations" go={this.go} />
           <Covid id="covid" go={this.go} />
           <Weather id="weather" go={this.go} />
           <News id="news" go={this.go} />
           <Education id="education" go={this.go} />
           <Taxi id="taxi" go={this.go} />
           <Example id="example" go={this.go} />
           <RoomOne id="ro" go={this.go} />
           <RusLang id="rl" go={this.go} />
           </View>
           </ConfigProvider>

        )
    }
}

export default App;
